*,
*:after,
*:before {
    box-sizing: border-box;
}

.thm-font-size-small {
    font-size: 16px;
}

.thm-font-size-medium {
    font-size: 19px;
}

.thm-font-size-large {
    font-size: 24px;
}

body,
html {
    height: 100%;
    margin: 0;
    padding: 0;
}

hr {
    display: block;
    width: 100%;
    border: none;
    height: 1px;
    background-image: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(50%, rgba(0, 0, 0, .5)), to(transparent));
    background-image: -webkit-linear-gradient(left, transparent, rgba(0, 0, 0, .5) 50%, transparent);
    background-image: linear-gradient(to right, transparent, rgba(0, 0, 0, .5) 50%, transparent);
}

button {
    font-size: inherit;
    font-family: inherit;
}

a {
    text-decoration: none;
    box-shadow: 0 -2px 0 inset var(--thm-bgPL);
}

a, button {
    border: 2px solid transparent;
}
a::-moz-focus-inner,
button::-moz-focus-inner {
    outline: 0;
    border: 0;
}
a:focus,
a:active,
button:focus,
button:active {
    text-decoration: underline;
    -webkit-text-decoration-style: dotted;
            text-decoration-style: dotted;
    border: 2px solid var(--thm-black3);
    outline: 0;
}

/*********************/
/* Responsive Design */
@media (max-width: 319px) {
    .hide-if-lt-320 {
        display: none;
    }
}

@media (min-width: 320px) {
    .hide-if-gt-320 {
        display: none;
    }
}

@media (max-width: 479px) {
    .hide-if-lt-480 {
        display: none;
    }
}

@media (min-width: 480px) {
    .hide-if-gt-480 {
        display: none;
    }
}

@media (max-width: 639px) {
    .hide-if-lt-640 {
        display: none;
    }
}

@media (min-width: 640px) {
    .hide-if-gt-640 {
        display: none;
    }
}

@media (max-width: 799px) {
    .hide-if-lt-800 {
        display: none;
    }
}

@media (min-width: 800px) {
    .hide-if-gt-800 {
        display: none;
    }
}

/****************************/
/** Material Design Themes **/
/****************************/
h1,
h2,
h3 {
    font-weight: bold;
}

h1 {
    font-size: 120%;
}

h2 {
    font-size: 110%;
}

h3 {
    font-size: 100%;
}

